.sidebar {
    display: flex;
    flex-direction: column;
    height: 100%;
}

.header {
    padding: 15px;
    border-bottom: 1px solid #f0f0f1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.profile {
    display: flex;
    align-items: center;
    gap: 10px;
}

.avatar {
    width: 36px;
    height: 36px;
    background: #f0f0f1;
    border-radius: 50%;
    border: 1px solid #dcdcde;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #646970;
}

.userInfo {
    display: flex;
    flex-direction: column;
}

.userName {
    font-weight: 600;
    font-size: 14px;
    color: #1d2327;
}

.userEmail {
    font-size: 11px;
    color: #646970;
}

.logoutBtn {
    border: none;
    background: none;
    cursor: pointer;
    color: #d63638;
    font-size: 16px;
}

.actions {
    padding: 15px;
    border-bottom: 1px solid #f0f0f1;
}

.ticketList {
    flex: 1;
    overflow-y: auto;
}

.ticketItem {
    padding: 15px;
    border-bottom: 1px solid #f0f0f1;
    cursor: pointer;
    transition: background 0.2s;
}

.ticketItem:hover {
    background: #f8f9fa;
}

.active {
    background: #f0f6fc;
    /* Light blue tint */
    border-left: 3px solid #2271b1;
}

.ticketHeader {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    font-size: 11px;
    color: #646970;
}

.ticketId {
    font-weight: 600;
    color: #2271b1;
}

.ticketSubject {
    margin: 0 0 8px 0;
    font-size: 14px;
    font-weight: 500;
    color: #1d2327;
}

.ticketFooter {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.category {
    font-size: 10px;
    background: #f0f0f1;
    padding: 2px 5px;
    border-radius: 3px;
    color: #646970;
}