.dashboard {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.statsGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 20px;
}

.statCard {
    background: white;
    padding: 20px;
    border-radius: 4px;
    border: 1px solid #dcdcde;
    display: flex;
    align-items: center;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
}

.statIcon {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    font-size: 20px;
    color: #1d2327;
}

.statInfo h3 {
    margin: 0;
    font-size: 24px;
    font-weight: 600;
    color: #1d2327;
}

.statInfo p {
    margin: 0;
    color: #646970;
    font-size: 13px;
}

.chartsGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    gap: 20px;
}